import {
    GET_UPDATE_CART,
    UPDATE_CART
} from "../constants/ActionTypes";


export default function cartReducer(state = {
    products: [],
    sum: null

}, action) {
    switch (action.type) {
        case UPDATE_CART:
            const product = action.product
            const newCart = {
                products: product.lineItems,
                sum: product.lineItemsSubtotalPrice.amount
            }

            return newCart;

        default:
    }
    return state;
}
