import * as types from '../constants/ActionTypes'


const sidebarReducerDefaultState = {
    open: false,
};

const cartSidebar = (state = sidebarReducerDefaultState, action) => {
    switch (action.type) {
        case types.OPEN_CART_SIDEBAR:
            return {
                ...state,
                open: action.open
            };
        default:
            return state;
    }
}

export default cartSidebar;
