import React, {useState, useEffect} from 'react';
import {Col, Row} from "react-bootstrap";
import {Link} from "react-router-dom";

import { Animated } from 'react-animated-css';


export default function CartPopOutItem(props) {
    const [amount, setAmount] = useState(props.item.quantity)
    const [tentative, setTentative] = useState(props.item.quantity)
    useEffect(() => {
        if (tentative !== props.item.quantity)
            setTentative(props.item.quantity)

        return () => {

        };
    }, [props.item.quantity]);

    const getVariantSize = (item) => {
        if (item.variant.selectedOptions[0].name === "Size") {
            return item.variant.selectedOptions[0].value
        }
        if (item.variant.selectedOptions[1].name === "Size") {
            return item.variant.selectedOptions[1].value
        }
    }

    const setQuantity = (type, amount, passThrough) => {

        switch (type) {
            case "strict":
                setAmount(props.item.quantity)
                break
            case "tentative plus":
                setTentative(props.item.quantity + 1)
                props.handleChange(props.item, passThrough)
                break;
            case "tentative minus":
                if (props.item.quantity - 1 > 0) {
                    setTentative(props.item.quantity - 1)
                    props.handleChange(props.item, passThrough)
                }
                break;
        }
    }

    return (
        <Row key={props.index} style={{marginTop:10, marginBottom:10, flex:1, display:"flex"}}>
            <Animated style={{flex:1, display:"flex"}}
                      animationIn="fadeInRight" animationOut="fadeOutRight" isVisible={props.tempCartList[props.index]}>
                <Link to={`/product/${props.item.id}`}>
                    <img
                        alt=""
                        className="mr-3 img-fluid" src={`${props.item.variant.image.src}`}
                        style={{width:75, height:100}}
                    />
                </Link>
                <Row style={{display:"flex", flex:1}}>
                    <Col style={{flex:7}}>
                        <p style={{color:"black"}}>{props.item.title}</p>
                        <Row style={{justifyContent:"space-around"}}>
                            <p style={{color:"black"}}>{props.item.variant.price}
                                {
                                    props.item.variant.compareAtPrice &&
                                    <span className={"strikethrough"} style={{color:"red", fontSize:10}}>{props.item.variant.compareAtPrice}</span>
                                }
                            </p>
                            <p style={{marginLeft:25, color:"black"}}>Size: {getVariantSize(props.item)}</p>
                        </Row>
                        <Row style={{justifyContent:"center", alignItems:"center"}}>
                            <p className={"popoutCartButtons"} style={{borderWidth:1, color:"black", textAlign:"center",height:20,width:20, borderRadius:35, borderStyle:"solid", marginLeft:0}} onClick={() => setQuantity("tentative minus", props.item, "subtract")}>-</p>
                            <p style={{textAlign:"center", color:"black", marginLeft:10, marginRight:10}}>{tentative}</p>
                            <p className={"popoutCartButtons"} style={{borderWidth:1, color:"black", textAlign:"center",height:20,width:20, borderRadius:35, borderStyle:"solid"}} onClick={() => setQuantity("tentative plus", props.item, "plus")}>+</p>
                        </Row>
                    </Col>
                    <Col style={{flex:1}} onClick={() => props.handleRemoveFromCart(props.item, props.index)}>
                        <p style={{cursor:"pointer", color:"black"}} >x</p>
                    </Col>
                </Row>
            </Animated>
        </Row>
    )
}
