import React, {Component} from 'react';
import Slider from 'react-slick';
import { Link } from "react-router-dom";

import Breadcrumb from "../common/breadcrumb";

class UnderConstruction extends Component {

    constructor (props) {
        super (props)

    }

    render (){

        return (
            <div>
                <Breadcrumb title={''} />

                <section className="p-0">
                    <div className="container">
                        <div className="row">
                            <div className="col-sm-12">
                                <div className="error-section">
                                    <h2 style={{fontFamily:"Poppins"}}>Under Construction!</h2>
                                    <h4 style={{fontFamily:"Poppins"}}>Sorry! This feature isn't quite ready!</h4>
                                    <p style={{marginTop:15, fontFamily:"Poppins"}}>This website is under active development! Don't worry though! It should be ready very soon!</p>

                                    <p>If you placed an order with us you'll receive email alerts with
                                    updates! If you need to login, feel free to login to our old site <a href="https://pretty-girl-stores.myshopify.com/" style={{color:"#37f0ed", fontWeight:"bold"}} >here.</a></p>
                                    <Link to={"/home"} className="btn btn-solid mt-5">back to home</Link>
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
            </div>
        )
    }
}

export default UnderConstruction
