export default class ReviewSlider extends React.Component {
    constructor() {
        super();
        this.state = {

        }
    }

    randomizeReviews = () => {
        const imgBuilder = [
            one,two,three,four,five,six,seven,eight,nine,ten,
            eleven,twelve,thirteen,fourteen,fifteen,sixteen,seventeen,
            eighteen,nineteen, twenty
        ];

        return imgBuilder
    }



    render() {
        return (
            <Container>
                <Col xs={12} className={"my-5 mx-auto"}>
                    <h4 className={"text-center mb-5"} >See what our customers are saying!</h4>
                    <div>
                        <Slider style={{}} className="home-slider mb-4">
                            {this.randomizeReviews().map((img, index) => (
                                <div>
                                    <img key={index} className={"img-fluid"} style={{}} src={img}/>
                                </div>

                            ))}
                        </Slider>
                    </div>
                </Col>


            </Container>
        )
    }
}
