export const colorChart = (color) => {
    const multiColorCheck = color.split("/")

    if (multiColorCheck.length === 1) {
        return returnColorHex(color)
    }
    if (multiColorCheck.length >= 2) {
        const colorHexArr = []
        for (let i=0; i < multiColorCheck.length; i++) {
            colorHexArr.push(returnColorHex(multiColorCheck[i]))
        }
        return colorHexArr
    }


}

// Get Unique Colors from Json Data
export const getColors = (products) => {
    try {
        const uniqueColors = [];
        products.map((product, index) => {
            let colorIndex = null;

            try {
                if (product.options[0].name === "Color")
                    colorIndex = 0;
            }
            catch {

            }

            try {
                if (product.options[1].name === "Color") {
                    colorIndex = 1;
                }
            }
            catch {

            }

            if (typeof colorIndex === "number") {
                if(product.options[colorIndex].values.length) {
                    product.options[colorIndex].values.map((color) => {
                        const formatColor = color.value.toLowerCase().trim();
                        if (uniqueColors.indexOf(formatColor) === -1) {
                            if (formatColor !== "multicolor") {
                                uniqueColors.push(formatColor);
                            }
                        }
                    })
                }
            }
        })
        return uniqueColors;

    }
    catch(e) {
        console.log("error getting color of object")
        console.log(e)
        return [];
    }
}

// Get Minimum and Maximum Prices from Json Data
export const getMinMaxPrice = (products) => {
    let min = 1, max = 35;
    try {
        products.map((product, index) => {
            let v = parseFloat(product.variants[0].price);
            min = (v < min) ? v : min;
            max = (v > max) ? v : max;
        })

        return {'min':min, 'max':max};
    }
    catch(e) {
        console.log("error in min max slider")
        console.log(e)
    }

}

export const getVisibleproducts = (data, { productType , brand, color, value, sortBy }) => {
    const name = brand;
    return data.products.filter(product => {
        let productMatch = true;
        let colorMatch = true;
        let nameMatch = true;


        if (productType) {
            productMatch = false;
            if(product.tags.length) {
                for (let i=0; i < product.tags.length; i++) {
                    if (product.tags.length <= 0) break;
                    if (typeof productType === "object") {
                        for (let a=0; a < productType.length; a++) {
                            const productLower = product.tags[i].value.toLowerCase().trim();
                            const nameLower = productType[a].toLowerCase().trim()

                            const productNoWhite = productLower.replace(/\s/g, '')
                            const nameNoWhite = nameLower.replace(/\s/g, '')
                            if (productNoWhite.includes(nameNoWhite)) {
                                productMatch = true;
                            }
                        }
                    }
                    else {
                        const productLower = product.tags[i].value.toLowerCase().trim();
                        const nameLower = productType.toLowerCase().trim()

                        const productNoWhite = productLower.replace(/\s/g, '')
                        const nameNoWhite = nameLower.replace(/\s/g, '')

                        if (productNoWhite.includes(nameNoWhite)) {
                            productMatch = true;
                        }
                    }
                }
                if (!productMatch) {
                    switch(product.productType) {
                        case "Dress":
                            if (productType === "Dresses") productMatch = true;
                            break;

                    }
                }
            }
            else productMatch = false;
        }
        if (name) {
            nameMatch = false;
            if(product.title) {
                const productLower = product.title.toLowerCase().trim();
                const nameLower = name.toLowerCase().trim()

                const productNoWhite = productLower.replace(/\s/g, '')
                const nameNoWhite = nameLower.replace(/\s/g, '')

                if (productNoWhite.includes(nameNoWhite)) {
                    nameMatch = true;
                }
                if (productNoWhite === nameNoWhite) {
                    nameMatch = true;
                }
                else if(product.tags.length) {
                    for (let i=0; i < product.tags.length; i++) {
                        if (product.tags.length <= 0) break;
                        const productTagLower = product.tags[i].value.toLowerCase().trim()
                        const tagNoWhite = productTagLower.replace(/\s/g, '')

                        if (tagNoWhite.includes(nameNoWhite)) {
                            nameMatch = true;
                        }
                    }
                }
            }
        }


        let colorIndex = null;
        if (color) {
            colorMatch = false;
                try {

                    if (product.options[0].name === "Color")
                        colorIndex = 0;
                }
                catch {
                }
                try {
                    if (product.options[1].name === "Color") {
                        colorIndex = 1;
                    }
                }
                catch {
                }
            }
            if (typeof colorIndex === "number") {
                for (let i = 0; i < product.options[colorIndex].values.length; i++) {
                    const productColor = product.options[colorIndex].values[i].value.toLowerCase().trim()
                    if (color === productColor) {
                        colorMatch = true;
                    }

                }
            }

        else colorMatch = true;
            let startPriceMatch;
            let endPriceMatch;

        if (value) {
             startPriceMatch = typeof value.min !== 'number' || value.min <= product.variants[0].price;
             endPriceMatch = typeof value.max !== 'number' || product.variants[0].price <= value.max;
        }
        else {
            startPriceMatch = true;
            endPriceMatch = true;
        }


 //       return brandMatch && colorMatch && startPriceMatch && endPriceMatch;
        return colorMatch && nameMatch && productMatch && startPriceMatch && endPriceMatch;
    }).sort((product1, product2) => {
        if (sortBy === 'HighToLow') {
            const product2Price = parseFloat(product2.variants[0].price)
            const product1Price = parseFloat(product1.variants[0].price)
            return product2Price < product1Price ? -1 : 1;
        } else if (sortBy === 'LowToHigh') {
            const product2Price = parseFloat(product2.variants[0].price)
            const product1Price = parseFloat(product1.variants[0].price)
            return product2Price > product1Price ? -1 : 1;
        } else if (sortBy === 'Newest') {
            return product2.id < product1.id ? -1 : 1;
        } else if (sortBy === 'AscOrder') {
            return product1.title.localeCompare(product2.title);
        } else if (sortBy === 'DescOrder') {
            return product2.title.localeCompare(product1.title);
        } else{
            return product2.id > product1.id ? -1 : 1;
        }
    });
}

export const getCartTotal = client => {
    const cartID = localStorage.getItem("checkout");
    if (cartID && client.client.checkout.fetch) {
        client.client.checkout.fetch(cartID).then((checkout) => {
            return checkout.lineItemsSubtotalPrice.amount
        });
    }

}

// Get Trending Tag wise Collection
export const getTrendingTagCollection = (products, type, tag) => {
    const items = products.filter(product => {
        return product.category === type && product.tags.includes(tag);
    })
    return items.slice(0,8)
}

// Get Trending Collection
export const getTrendingCollection = (products, type) => {
    const items = products.filter(product => {
        return product.productType === type;
    })
    return items.slice(0,8)
}

// Get Special 5 Collection
export const getSpecialCollection = (products, type) => {
    const items = products.filter(product => {
        return product.productType === type;
    })
    return items.slice(0,5)
}

// Get TOP Collection
export const getTopCollection = products => {
    const items = products.filter(product => {
        return product.rating > 4;
    })
    return items.slice(0,8)
}

// Get New Products
export const getNewProducts = (products, type) => {
    const createdAt = moment(Date.now());

    const items = products.filter(product => {
        return createdAt.diff(product.createdAt, "hours") < 1344 && product.category === type;
    })

    const itemsShuffle = items.sort(function(){
        return 0.5 - Math.random();
    });
    console.log("returning new products")

    return items.slice(0,8)
}

