// Get Womens Wear
export const getWomensWear = products => {
    const items = products.sort(function(product){
        let productMatch = false;
        if(product.tags.length) {
            for (let i=0; i < product.tags.length; i++) {

                if (product.tags.length <= 0) break;
                const productLower = product.tags[i].value.toLowerCase().trim();
                const searchTerm = "winter";

                const productNoWhite = productLower.replace(/\s/g, '')

                if (productNoWhite.includes(searchTerm)) {
                    productMatch = true;
                }
            }
        }
        else productMatch = false;

        return productMatch

    });

    return items.slice(0, 8)
}

// Get Single Product
export const getSingleItem = (products, id) => {

    const items = products.find((element) => {
        return element.id === id;
    })
    return items;
}

// Get Feature Products
export const getFeatureImages = (products, type) => {

    const items = products.filter(product => {
        return product.type === type;
    })
    return items;
}


