import React from 'react';
import {getCartTotal} from "../../services";
import {connect} from "react-redux";
import {decrementQty, incrementQty, removeFromCart, openCartSidebar, changeQuantityCart} from "../../actions";
import { Animated } from 'react-animated-css';
import CartPage from "../common/headers/common/cart-header";
import {Link} from "react-router-dom";
import { Row, Container, Col } from "react-bootstrap";
import UrgencyBar from "./Urgency Bar";
import CartPopOutItem from "./cartPopoutItem";
import ReactGA from 'react-ga';

class CartPopout extends React.Component {
    constructor() {
        super();
        this.state = {
            cartOpen: false,
            interactable: false,
            tempCartList : []
        }
    }

    componentDidMount() {
        const saveList = [];
        if (this.props.cartList.products) {
            for (let i=0; i < this.props.cartList.products.length; i++) {
                saveList.push(true)
            }
            this.setState({tempCartList: saveList})
        }

    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if (this.props.cart && !this.state.cartOpen) {
            this.setState({cartOpen: true, interactable: true})
        }
        else if (!this.props.cart && this.state.cartOpen) {
            this.setState({cartOpen:false}, () => {
                setTimeout(() => {
                    this.setState({interactable: false})
                }, 750)
            })
        }
        if (this.props.cartList.products) {
            if (this.props.cartList.products.length !== this.state.tempCartList.length) {
                const saveList = [];
                for (let i=0; i < this.props.cartList.products.length; i++) {
                    saveList.push(true)
                }
                this.setState({tempCartList: saveList})
            }
        }


    }

    getWindowSize = () => {
        const windowSize = this.props.window.width

        if (windowSize < 250) return "25vh"
        if (windowSize < 450) return "35vh"
        if (windowSize < 550) return "45vh"
        if (windowSize < 750) return "35vh"
    }

    handleRemoveFromCart = (item,index) => {
        const temp = this.state.tempCartList;
        temp[index] = false;
        if (item) {
            if (item.title) {
                ReactGA.event({
                    category: 'User',
                    action: item.title,
                    label: "Remove From Cart"
                });
            }
        }
        this.setState({tempCartList: temp}, () => {
            this.props.removeFromCart(item, this.props.client)
        })

    }

    handleChange = (item, type) => {
        switch (type) {
            case "plus":
                this.props.changeQuantityCart(item, item.quantity + 1, this.props.client)
                break;
            case "subtract":
                if (item.quantity - 1 > 0) {
                    this.props.changeQuantityCart(item, item.quantity - 1, this.props.client)
                }
                break;
        }
    }

    closeNav = () => {
        this.
        props.openCartSidebar(false)
    }
    goToCheckout = () => {
        const checkoutID = localStorage.getItem("checkout")
        ReactGA.event({
            category: 'User',
            action: 'Checkout',
            label:"Clicked Checkout"
        });
        if (checkoutID) {
            this.props.client.client.checkout.fetch(checkoutID).then((checkout) => {
                // Do something with the checkout
                window.open(checkout.webUrl)
            });
        }
    }

    render() {
        return(
            <Animated animationIn="fadeInRight" animationOut="fadeOutRight" className={"cartPopOut"} isVisible={this.state.cartOpen} style={{height:"100%", width: this.getWindowSize, position:"fixed", display:"flex", top:0, zIndex:this.state.interactable ? 99999 : -1, right:0, backgroundColor:"white"}}>
                <div style={{display:"flex", flex:1, flexDirection:"column"}}>

                    {/*Upper Component*/}
                    <Animated style={{flex:1, display:"flex",justifyContent:"center", alignItems:"center"}}
                              animationIn="fadeInRight" animationOut="fadeOutRight" isVisible={true}>
                        <Col>
                            <h4 style={{color:"black", textAlign:"center"}}>Free Shipping</h4>

                            <Col style={{flex:1}}>
                                <div style={{flex:1}}>
                                    <UrgencyBar price={this.props.cartList.sum} maxBar={35} />
                                </div>
                            </Col>
                        </Col>


                    </Animated>

                    {/*Middle Component*/}

                    <Animated style={{flex:4, display:"flex", overflow:"hidden"}} animationIn="fadeInRight" animationOut="fadeOutRight" isVisible={true}>
                        <Container id={"cartPopOut"} style={{overflowY: "auto", overflowX:"hidden", borderTopWidth:1, borderBottomWidth:1, borderStyle:"solid"}}>
                            {
                                this.props.cartList.products  ?
                                    this.props.cartList.products.map((item,index) =>
                                        <CartPopOutItem key={index} item={item} index={index} tempCartList={this.state.tempCartList} handleRemoveFromCart={this.handleRemoveFromCart} handleChange={this.handleChange}/>)
                                :
                                    <Col style={{marginTop:10, marginBottom:10, flex:1, display:"flex"}}>
                                        <h4 style={{color:"gray"}}>Nothing here! Get to shopping!</h4>
                                    </Col>
                            }
                        </Container>
                    </Animated>
                    {/*End Component*/}
                    <Animated style={{flex:2, display:"flex",justifyContent:"center", alignItems:"center"}} animationIn="fadeInRight" animationOut="fadeOutRight" isVisible={true}>
                        <div style={{flex:1, display:"flex", flexDirection:"column"}}>
                            {
                                this.props.cartList.products ?
                                    this.props.cartList.products.length > 0 ?
                                        <div style={{marginBottom:15}} className="total">
                                            <h5 style={{color:"black", marginLeft:5, marginTop:10, marginBottom:10, textAlign:"center"}}>Subtotal : <span>{this.props.symbol}{this.props.cartList.sum}</span></h5>
                                        </div> :""
                                    :
                                    ""
                            }


                            {
                                this.props.cartList.products ?
                                    this.props.cartList.products.length > 0 ?
                                        <div>
                                            <button style={{flex:1, width:"80%", marginBottom:15, marginTop:15}} disabled={false} onClick={() => this.goToCheckout()} className={"btn btn-dark d-block mx-auto"}>
                                                Checkout
                                            </button>
                                        </div>
                                        :
                                        <button style={{flex:1, width:"80%", marginBottom:15, marginTop:15}} disabled={true} className={"btn btn-dark d-block mx-auto"}>
                                            Checkout
                                        </button>
                                    :""
                            }

                            <button onClick={() => this.closeNav()} style={{flex:1, marginBottom:10, width:"80%"}} className={"btn btn-outline-dark d-block mx-auto"}>
                                Back
                            </button>
                        </div>
                    </Animated>
                </div>
            </Animated>
        )
    }
}

const mapStateToProps = (state) => ({
    cartList: state.cartList,
    symbol: state.data.symbol,
    total: getCartTotal(state.client),
    client: state.client,
    cart: state.cartSidebar.open,
    window: state.window.window
})

export default connect(
    mapStateToProps,
    {removeFromCart, incrementQty, decrementQty, openCartSidebar, changeQuantityCart}
)(CartPopout)
