import React, {useEffect, useState} from 'react';
import {Col, Row} from "react-bootstrap";
import NavBar from "./navbar";
import {connect} from "react-redux";


function CustomNavBar(props) {

    if (props.window.width > 1184)
        return (
            <div style={{position:"relative", marginBottom:40, marginTop: -40}}>
                <header className={"fixed"} style={{}}>
                    <div className={"main-menu"} style={{justifyContent:"center", position:"sticky", top:0, left:0, right:0}}>
                        <div className={""}>
                            {/*Top Navigation Bar Component*/}
                            <NavBar navOpened={props.navOpened} setNav={(state) => props.setNav(state)}/>
                        </div>
                    </div>
                </header>
            </div>


        )
    else return <div></div>
}

const mapStateToProps = (state) => ({
    window: state.window.window
})

export default connect(
    mapStateToProps,
)(CustomNavBar)


