import React, {Component} from 'react';

import {
    svgFreeShipping,
    svgservice,
    svgoffer,
    svgpayment
} from "../../../services/script"
import {Link} from "react-router-dom";


class Service extends Component {
    render (){

        return (
            <div className="collection-filter-block ">
                <div className="product-service">
                    <div className="media">
                        <div dangerouslySetInnerHTML={{ __html: svgFreeShipping }} />
                        <a href={"https://pretty-girl-stores.myshopify.com/pages/shipping"}>
                            <div className="media-body">
                                <h4>free shipping</h4>
                                <p>free shipping within the US</p>
                            </div>
                        </a>
                    </div>
                    <div className="media">
                        <div dangerouslySetInnerHTML={{ __html: svgservice }} />
                        <div className="media-body">
                            <h4>24 X 7 service</h4>
                            <p>Friend and helpful service</p>
                        </div>
                    </div>
                    <div className="media">
                        <div dangerouslySetInnerHTML={{ __html: svgoffer }} />
                        <div className="media-body">
                            <h4>Holiday deals</h4>
                            <p>Get the best bang for your buck!</p>
                        </div>
                    </div>
                    <div className="media border-0 m-0">
                        <div dangerouslySetInnerHTML={{ __html: svgpayment }} />
                        <div className="media-body">
                            <h4>online payment</h4>
                            <p>Easy payment and shipping</p>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default Service;
