import React, {Component} from 'react';
import {Link} from 'react-router-dom';
import { withTranslate } from 'react-redux-multilingual'
import {connect} from "react-redux";
import {openCartSidebar, removeFromCart} from "../../../../actions";
import { Alert, Container } from "react-bootstrap";

class TopBar extends Component {

    render() {
        const {translate} = this.props;
        return (
            <div>
                <div className="top-header">
                    <Container fluid style={{padding:15}}>
                        <div className="row" style={{paddingLeft:25, paddingRight:25}}>
                            <div className="col-lg-6">
                                <div className="header-contact">
                                    <ul>
                                        <li>{translate('topbar_title', { theme_name: ' Pretty Girl Stores ' })}</li>
                                        <li><i className="fa fa-phone" aria-hidden="true"></i>{translate('call_us')} (+1 270-972-4755)</li>
                                    </ul>
                                </div>
                            </div>
                            <div className="col-lg-6 text-right">
                                <ul className="header-dropdown">
                                    <li className="mobile-wishlist"><Link to={`/wishlist`}><i className="fa fa-heart" aria-hidden="true"></i>{translate('wishlist')}</Link></li>
                                    <li className="onhover-dropdown mobile-account">
                                        <i className="fa fa-user" aria-hidden="true"></i> {translate('my_account')}
                                        <ul className="onhover-show-div">
                                            <li>
                                                <Link to={`/under-construction`} data-lng="en">Login</Link>
                                            </li>
                                            <li>
                                                <Link to={`/under-construction`} data-lng="en">Register</Link>
                                            </li>
                                        </ul>
                                    </li>
                                </ul>
                            </div>
                        </div>
                    </Container>
                </div>
            </div>
        )
    }
}


function mapStateToProps(state) {
    return {
        cartList: state.cartList,
        symbol: state.data.symbol,
        client: state.client,
        cartSidebar: state.cartSidebar.open
    }
}

export default connect(mapStateToProps, {removeFromCart, openCartSidebar})(withTranslate(TopBar));

