import React, {Component, lazy, Suspense} from 'react';
import { connect } from 'react-redux'
import {Link} from 'react-router-dom';
import InfiniteScroll from 'react-infinite-scroll-component';
import { Spinner, Carousel } from "react-bootstrap";

import {addToCart, addToWishlist, addToCompare, filterType, filterBrand, filterColor, filterPrice} from '../../../actions'
import {getVisibleproducts} from '../../../services';

import { getSpecificProducts } from "../../../actions";

import {collectionEndPoints} from "../../../api/data";

const ProductItem = lazy(() => import('../../layouts/common/product-item'));

class ProductListing extends Component {

    constructor (props) {
        super (props)

        this.state = {
            location: "",
            limit: 12,
            hasMoreItems: true,
            loading: true,
            search: false,
            timeout:null
        };
    }

    componentWillMount(){
        this.props.filterColor(null)
        this.loadItems();
        this.fetchMoreItems();
        this.setState({limit: this.props.products.length, loading: true})
        this.loadingTimer()
    }

    componentWillUnmount() {
        if (this.state.timeout){
            clearTimeout(this.state.timeout);
        }
    }


    loadingTimer = () => {
        if (!this.props.products.length) {
            setTimeout(() => {
                this.setState({loading:false})
            }, 1000)
        }
        else {
            this.setState({loading:false})
        }
    }

    loadItems = () => {
        this.setState({location: this.props.location.pathname, limit: this.props.products ? this.props.products.length : 0})
        const location = this.props.location.pathname;
        if (location.includes("search")) {
            const search = location.replace("/search/", "")
            this.props.filterType(null)
            this.props.filterBrand(search)
            this.props.filterPrice({value:{min:0, max:150}})
            this.setState({timeout: timeout})
            const timeout = setTimeout(() => {
                this.setState({search:true})
            }, 750)

        }
        else {
            this.props.filterBrand(null)
            this.props.filterPrice({value:{min:1, max:35}})
            this.props.filterType(collectionEndPoints[this.props.location.pathname])
            this.setState({search:false})

        }

    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if (this.state.location !== this.props.location.pathname) {
            this.props.filterBrand(null)
            this.props.filterColor(null)
            this.props.filterType(null)
            this.loadItems();
        }
        if (this.props.products.length !== this.state.limit) {
            this.setState({limit: this.props.products ? this.props.products.length : 0})
        }

    }


    fetchMoreItems = () => {
        if (this.state.limit >= this.props.products.length) {
            this.setState({ hasMoreItems: false });
            return;
        }
        // a fake async api call
        setTimeout(() => {
            this.setState({
                limit: this.state.limit + 5
            });
        }, 3000);


    }

    render (){
        const {products, addToCart, symbol, addToWishlist, addToCompare} = this.props;

        return (
            <div>
                <div className="product-wrapper-grid">
                    <div className="container-fluid">
                        {
                            products.length > 0 &&
                            <InfiniteScroll
                                dataLength={this.state.limit} //This is important field to render the next data
                                next={this.fetchMoreItems}
                                hasMore={this.state.hasMoreItems}
                                loader={<div className="loading-cls"></div>}
                            >
                                <div className="row">

                                    {products.slice(0, this.state.limit).map((product, index) =>
                                        <div
                                            className={`${this.props.colSize === 3 ? 'col-xl-3 col-md-6 col-grid-box' : 'col-lg-' + this.props.colSize}`}
                                            key={index}>
                                            <Suspense fallback={<div><Spinner/></div>}>
                                                <ProductItem product={product} symbol={symbol}
                                                             onAddToCompareClicked={() => addToCompare(product)}
                                                             onAddToWishlistClicked={() => addToWishlist(product)}
                                                             onAddToCartClicked={(item, quantity) => addToCart(item, quantity, this.props.client)} key={index} window={this.props.window}
                                                />
                                            </Suspense>
                                        </div>)
                                    }
                                </div>
                            </InfiniteScroll>
                        }

                        {

                                this.props.loading && this.props.search &&
                            <div className="row">
                                <div className="col-sm-12 text-center section-b-space mt-5 no-found" >
                                    <img src={`/assets/images/empty-search.jpg`} className="img-fluid mb-4" />
                                    <h3>Sorry! Couldn't find the product you were looking For!!!    </h3>
                                    <p>Please check if you have misspelt something or try searching with other words.</p>
                                    <Link to={`/`} className="btn btn-solid">continue shopping</Link>
                                </div>
                            </div>


                        }
                        {
                            !this.props.loading && products.length <= 0 &&
                            <div style={{justifyContent:"center", alignItems:"center", textAlign:"center"}}>
                                <Spinner animation="border" style={{marginTop:35}} />
                            </div>
                        }
                    </div>
                </div>
            </div>
        )
    }
}
const mapStateToProps = (state) => ({
    products: getVisibleproducts(state.data, state.filters),
    symbol: state.data.symbol,
    loading: state.loading,
    window: state.window.window,
    client: state.client
})

export default connect(
    mapStateToProps, {addToCart, addToWishlist, addToCompare, getSpecificProducts, filterType, filterBrand, filterColor, filterPrice}
)(ProductListing)
